* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  object-fit: contain;
  background-color: #fafafa;
  background-image: url("pexels-johannes.3eedd7cd.jpg");
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.bodyUl {
  object-fit: contain;
  width: 600px;
  height: 400px;
  background-color: #2c918f;
  border-radius: 40px;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  gap: 20px;
  margin: 15% auto 0;
  padding: 0;
  list-style: none;
  display: flex;
  position: relative;
  box-shadow: 0 3.43693px 3.43693px rgba(0, 0, 0, .25);
}

.bodyUl:after {
  z-index: -1;
  content: "";
  width: 600px;
  height: 400px;
  background-color: #fff;
  border-radius: 40px;
  position: absolute;
  top: 5px;
  left: 3px;
  box-shadow: inset 1px -1px 8px 3px rgba(0, 0, 0, .65);
}

.bodyLi {
  background-color: #2c918f;
  border-radius: 14px;
  margin-left: 40px;
  position: relative;
  overflow: hidden;
}

.bodyLink {
  width: 140px;
  height: 54px;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.bodyLink span {
  z-index: 1;
  width: 90%;
  height: 84%;
  background-color: #2ca3a3;
  background-color: linear-gradient(to right, #2ca3a3, #1d6b6b);
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  transition: background-color .3s;
  display: flex;
  position: relative;
  box-shadow: 0 3.43693px 3.43693px rgba(0, 0, 0, .25);
}

.bodyLink span:hover {
  background-color: #47ecec;
  transition: background-color .3s;
}

.bodyLink:before {
  content: "";
  width: 156px;
  height: 40px;
  background-color: rgba(0, 0, 0, 0);
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0) 50%, #fff 50%);
  background-position: 0 0, 100% 0;
  background-repeat: no-repeat;
  background-size: 100% 200%;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  position: absolute;
  transform: rotate(0deg);
  box-shadow: 0 3.43693px 3.43693px rgba(0, 0, 0, .25);
}

.bodyLink:hover:before {
  animation-name: spin;
  animation-duration: .6s;
  animation-timing-function: linear;
  animation-iteration-count: inherit;
}

.bodyLink:not(:hover):before {
  animation-name: spin-rev;
  animation-duration: .6s;
  animation-timing-function: linear;
  animation-iteration-count: inherit;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(180deg);
  }
}

@keyframes spin-rev {
  0% {
    transform: rotate(180deg);
  }

  100% {
    transform: rotate(0deg);
  }
}

